function to_slug(str) {
  // Chuyn ht sang ch thng
  str = str.toLowerCase();

  // xa du
  str = str.replace(/(||||||||||||||||)/g, "a");
  str = str.replace(/(||||||||||)/g, "e");
  str = str.replace(/(||||)/g, "i");
  str = str.replace(/(||||||||||||||||)/g, "o");
  str = str.replace(/(||||||||||)/g, "u");
  str = str.replace(/(||||)/g, "y");
  str = str.replace(/()/g, "d");

  // Xa k t c bit
  str = str.replace(/([^0-9a-z-\s])/g, "");

  // Xa khong trng thay bng k t -
  str = str.replace(/(\s+)/g, "-");

  // xa phn d -  u
  str = str.replace(/^-+/g, "");

  // xa phn d -  cui
  str = str.replace(/-+$/g, "");

  // return
  return str;
}

const numberWithCommas = (num) =>
  num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");

const sortLowToHigh = (arr) => {
  return arr.sort((a, b) =>
    a.price > b.price ? 1 : a.price < b.price ? -1 : 0
  );
};
const sortHighToLow = (arr) => {
  return arr.sort((a, b) =>
    a.price < b.price ? 1 : a.price > b.price ? -1 : 0
  );
};

// get random
const get_random = (arr, count) => {
  const max = arr.length - count;
  const min = 0;
  const start = Math.floor(Math.random() * (max - min) + min);
  return arr.slice(start, start + count);
};

const getAllProducts = (products) => products;

const getProducts = (count, products) => {
  const max = products.length - count;
  const min = 0;
  const start = Math.floor(Math.random() * (max - min) + min);
  return products.slice(start, start + count);
};

const getProductBySlug = (slug, products) =>
  products.find((e) => e.slug === slug);

const getCartItemsInfo = (cartItems) => {
  let res = [];
  if (cartItems.length > 0) {
    cartItems.forEach((e) => {
      let product = getProductBySlug(e.slug);
      res.push({
        ...e,
        product: product,
      });
    });
  }
  // console.log(res)
  // console.log('sorted')
  // console.log(res.sort((a, b) => a.slug > b.slug ? 1 : (a.slug < b.slug ? -1 : 0)))
  return res.sort((a, b) => (a.id > b.id ? 1 : a.id < b.id ? -1 : 0));
};
export {
  to_slug,
  sortLowToHigh,
  sortHighToLow,
  get_random,
  getCartItemsInfo,
  getProducts,
  getAllProducts,
  numberWithCommas,
};
