import React, { useState, useRef, useEffect, useContext } from "react";
import Skeleton from "react-loading-skeleton";
import { toast } from "react-toastify";
import moment from "moment";
import Helmet from "../../components/common/Helmet";
import Table from "../../components/common/Table";
import Button from "../../components/common/Button";
import Modal from "../../components/common/Modal";
import { numberWithCommas } from "../../utils/utils";
import { FormEdit, FormAdd } from "../../components/common/Forms";
import { ProductContext } from "../../provider/context/ProductContext";
import axios from "axios";

// data render ui for color, gift
const dataInput = [
  {
    type: "color",
    title: "Vng",
    value: "Vng",
  },
  {
    type: "color",
    title: "Hng",
    value: "Hng",
  },
  {
    type: "color",
    title: "Trng",
    value: "Trng",
  },
  {
    type: "color",
    title: "Hng + Trng",
    value: "Hng Trng",
  },
  {
    type: "color",
    title: "Vng + Trng",
    value: "Vng Trng",
  },
  {
    type: "color",
    title: "Hng + Vng",
    value: "Hng Vng",
  },
  {
    type: "gift",
    title: "Cho cha",
    value: "Cho cha",
  },
  {
    type: "gift",
    title: "Cho m",
    value: "Cho m",
  },
  {
    type: "gift",
    title: "Cho chng",
    value: "Cho chng",
  },
  {
    type: "gift",
    title: "Cho nng",
    value: "Cho nng",
  },
  {
    type: "gift",
    title: "Cho b",
    value: "Cho b",
  },
];
const ProductAdmin = () => {
  const [currentId, setCurrentId] = useState(null);
  const [isActiveForm, setIsActiveForm] = useState(false);
  const [isActiveFormAdd, setIsActiveFormAdd] = useState(false);
  const [activeGold, setActiveGold] = useState(false);
  const [cateJewel, setCateJewel] = useState(true);
  const [cateWatch, setCateWatch] = useState(false);
  const [cateAccessory, setCateAccessory] = useState(false);
  const [searchItem, setSearchItem] = useState("");
  const [avatar, setAvatar] = useState([]);
  const goldRef = useRef(null);
  const typeRef = useRef(null);
  const { addProducts, deleteProducts, updateProducts } =
    useContext(ProductContext);

  const [, setImgs] = useState([]);
  const [files, setFiles] = useState([]);
  const {
    productState: { products, product, loadingOneProduct },
    loadProduct_Id,
  } = useContext(ProductContext);
  let dataInit = {
    imgsUrl: [],
    type: "Trang sc",
    category: {
      brand: "",
      jewel_type: "Nhn",
      jewel_line: "Kim cng",
      material: "Bc",
      materialGold: "18k",
      accessory_type: "Mt knh",
    },
    price: "",
    gender: "Nam",
    name: "",
    color: ["Vng"],
    isCouple: false,
    gift: [],
    description: "",
    disableGift: true,
  };
  const [data, setData] = useState(dataInit);
  useEffect(() => {
    if (currentId) {
      loadProduct_Id(currentId);
    }
  }, [currentId]);

  useEffect(() => {
    if (loadingOneProduct == false)
      setData({
        ...data,
        ...product,
        disableGift: true,
      });
  }, [loadingOneProduct]);

  const notify = () =>
    toast(() => {
      if (
        data.imgsUrl.length === 0 ||
        data.price === "" ||
        data.description === ""
      ) {
        return "Thm tht bi, hy kim tra li!";
      } else {
        return "Thm thnh cng!";
      }
    });
  const notifyDel = () => toast("Xa thnh cng");
  useEffect(() => {
    if (typeRef.current) {
      if (typeRef.current.value === "Trang sc") {
        setCateJewel(true);
        setCateWatch(false);
        setCateAccessory(false);
      }
    }
  }, []);

  const updateProduct = () => {
    updateProducts(currentId, data);
  };

  const handleEvents = {
    // gold, sliver, platinum, alloy
    changMaterial: (e) => {
      if (goldRef.current) {
        if (goldRef.current.value === "Vng") {
          setActiveGold(true);
        } else {
          setActiveGold(false);
        }
      }
    },
    // jewels, watch, glasses, accessories
    changType: (e) => {
      if (typeRef.current) {
        if (typeRef.current.value === "Trang sc") {
          setCateJewel(true);
          setCateWatch(false);
          setCateAccessory(false);
        } else if (typeRef.current.value === "ng h") {
          setCateJewel(false);
          setCateWatch(true);
          setCateAccessory(false);
        } else {
          setCateJewel(false);
          setCateWatch(false);
          setCateAccessory(true);
        }
      }
      handleEvents.getData(e);
    },
    // add images
    changeImg: (e) => {
      if (e.target.files && !data.imgsUrl.includes(e.target.files[0].name)) {
        const fileArray = Array.from(e.target.files).map((file) => {
          return URL.createObjectURL(file);
        });
        setFiles([...files, e.target.files[0]]);
        setAvatar((prevImg) => prevImg.concat(fileArray));
        setImgs(avatar);
        Array.from(e.target.files).map((file) => URL.revokeObjectURL(file));
      }
      handleEvents.getData(e);
    },
    previewAvatar: (img) => {
      return img.map((e, id) => {
        return (
          <div
            key={id}
            className="col col-xxl-3 col-xl-3 col-md-4 col-sm-6 mt-4"
            style={{ overflow: "hidden", border: "1px solid #ccc" }}
          >
            <img src={e} alt="" style={{ width: "100%", height: "100%" }} />
          </div>
        );
      });
    },

    //get data
    getData: (e) => {
      if (
        [
          "brand",
          "jewel_type",
          "jewel_line",
          "material",
          "materialGold",
          "accessory_type",
        ].includes(e.target.className)
      ) {
        setData({
          ...data,
          category: {
            ...data.category,
            [e.target.className]: e.target.value,
          },
        });
      } else {
        if (["color", "gift"].includes(e.target.className)) {
          const className = e.target.className;
          if (e.target.checked) {
            setData({
              ...data,
              [className]: [...data[className], e.target.value],
            });
          } else {
            setData({
              ...data,

              [className]: data[className].filter((v) => v !== e.target.value),
            });
          }
        } else if (e.target.className === "custom__input__file__form__add") {
          setData({
            ...data,
            imgsUrl: [...data.imgsUrl, e.target.files[0].name],
          });
        } else
          setData({
            ...data,
            [e.target.className]: e.target.value,
          });
      }
    },
    // submit
    submit: () => {
      addProducts(data);
      files.map((file) => {
        const imageData = new FormData();
        imageData.append("myFile", file);
        axios.post("http://localhost:5000/api/uploadfile", imageData);
      });
      setData(dataInit);
    },
    checkValid: (e) => {
      e.preventDefault();
      if (
        data.imgsUrl.length === 0 ||
        data.price === "" ||
        data.description === ""
      ) {
      } else {
        handleEvents.submit(e);
      }
    },
  };

  return (
    <Helmet title="Qun l sn phm">
      <div className="product__admin">
        <div className="container mt-4">
          <div className="row">
            <div className="col col-xl-12 col-md-12 col-sm-12 ">
              <Table title="Danh sch sn phm">
                <input
                  type="text"
                  placeholder="Tm kim sn phm..."
                  onChange={(e) => setSearchItem(e.target.value)}
                />
                <Button
                  onClick={() => setIsActiveFormAdd(true)}
                  content="Thm sn phm"
                  classNameBtn="ms-4 btn__add"
                />

                {/* =============add product ================== */}

                {isActiveFormAdd ? (
                  <Modal
                    style={{
                      backgroundImage:
                        "linear-gradient(-20deg, #00cdac 0%, #8ddad5 100%)",
                    }}
                    active={isActiveFormAdd}
                    setActive={setIsActiveFormAdd}
                  >
                    <FormAdd
                      handleClick={notify}
                      title="Thm thng tin sn phm"
                      onSubmit={(e) => {
                        handleEvents.checkValid(e);
                        setIsActiveFormAdd(false);
                        setData(dataInit);
                      }}
                    >
                      <li className="form__item">
                        <input
                          type="file"
                          className="custom__input__file__form__add"
                          id="custom__input__file__form__add"
                          multiple
                          accept="image/png,image/jpeg"
                          onChange={(e) => handleEvents.changeImg(e)}
                        />
                        <label
                          htmlFor="custom__input__file__form__add"
                          className="label__custom__input__file__form__add"
                        >
                          Thm nh
                        </label>
                      </li>
                      <li className="form__item">
                        <div className="row ">
                          {handleEvents.previewAvatar(avatar)}
                        </div>
                      </li>
                      <li className="form__item mt-4">
                        <input
                          onChange={(e) => handleEvents.getData(e)}
                          value={data.name}
                          type="text"
                          placeholder="Tn sn phm"
                          style={{ width: "100%" }}
                          className="name"
                        />
                      </li>
                      <li className="form__item">
                        <p>Loi sn phm:</p>
                        <select
                          ref={typeRef}
                          onChange={(e) => {
                            handleEvents.changType(e);
                            handleEvents.getData(e);
                          }}
                          value={data.type}
                          name="edit__type_product"
                          id="type_product"
                          className="type"
                        >
                          <option value="Trang sc">Trang sc</option>
                          <option value="ng h">ng h</option>
                          <option value="Ph kin">Ph kin</option>
                        </select>
                      </li>
                      <li className="form__item form__item__category">
                        {cateJewel === true && (
                          <>
                            <p>Loi trang sc:</p>
                            <select
                              onChange={(e) => handleEvents.getData(e)}
                              value={data.category.jewel_type}
                              className="jewel_type"
                            >
                              <option value="Nhn">Nhn</option>
                              <option value="Lc">Lc</option>
                              <option value="Bng tai">Bng tai</option>
                              <option value="Vng tay">Vng tay</option>
                              <option value="Dy c">Dy c</option>
                            </select>
                            <br />
                            <p>Dng trang sc:</p>
                            <select
                              className="jewel_line"
                              onChange={(e) => handleEvents.getData(e)}
                              value={data.category.jewel_line}
                            >
                              <option value="Kim cng">Kim cng</option>
                              <option value="Khng nh ">
                                Khng nh 
                              </option>
                              <option value="Ecz-cz">Ecz-cz</option>
                            </select>
                          </>
                        )}
                        {cateWatch === true && (
                          <>
                            <p>Thng hiu:</p>
                            <select
                              id="brand__watch"
                              className="brand"
                              onChange={(e) => handleEvents.getData(e)}
                              value={data.category.brand}
                            >
                              <option value="Casio">Casio</option>
                              <option value="Gucci">Gucci</option>
                              <option value="Citizen">Citizen</option>
                            </select>
                          </>
                        )}
                        {cateAccessory === true && (
                          <>
                            <p>Loi ph kin:</p>
                            <select
                              id="type_accessory"
                              className="accessory_type"
                              onChange={(e) => handleEvents.getData(e)}
                              value={data.category.accessory_type}
                            >
                              {" "}
                              <option value="Mt knh">Mt knh</option>
                              <option value="Tht Lng">Tht Lng</option>
                            </select>
                          </>
                        )}
                      </li>
                      <li className="form__item">
                        <p>Cht liu:</p>
                        <select
                          className="material"
                          name="edit__material"
                          id="material"
                          ref={goldRef}
                          onChange={(e) => {
                            handleEvents.getData(e);
                            handleEvents.changMaterial(e);
                          }}
                          value={data.category.material}
                        >
                          <option value="Bc">Bc</option>
                          <option value="Vng">Vng</option>
                          <option value="Hp kim cao cp">
                            Hp kim cao cp
                          </option>
                          <option value="Platinum">Platinum</option>
                        </select>
                        <br />
                        {activeGold === true && (
                          <>
                            <p>Cht liu vng:</p>
                            <select
                              name="material__gold"
                              id="material__gold"
                              className="materialGold"
                              onChange={(e) => handleEvents.getData(e)}
                              value={data.category.materialGold}
                            >
                              <option value="18k">18k</option>
                              <option value="22k">22k</option>
                              <option value="24k">24k</option>
                            </select>
                          </>
                        )}
                      </li>
                      <li className="form__item">
                        <p>Gii tnh:</p>
                        <select
                          id="gender"
                          className="gender"
                          onChange={(e) => handleEvents.getData(e)}
                          value={data.gender}
                        >
                          <option value="Nam">Nam</option>
                          <option value="N">N</option>
                          <option value="Unisex">Unisex</option>
                        </select>
                      </li>
                      <li className="form__item">
                        <p>Sn phm couple:</p>
                        <select
                          id="couple"
                          className="isCouple"
                          onChange={(e) => handleEvents.getData(e)}
                          value={data.isCouple}
                        >
                          <option value={true}>C</option>
                          <option value={false}>Khng</option>
                        </select>
                      </li>
                      <li className="form__item form__item__type__gifts">
                        <p>Loi qu tng: </p>

                        {dataInput.map(
                          (e, id) =>
                            e.type === "gift" && (
                              <React.Fragment key={id}>
                                <label htmlFor={e.title + id}>{e.title}</label>
                                <input
                                  onChange={(e) => handleEvents.getData(e)}
                                  className="gift"
                                  id={e.title + id}
                                  type="checkbox"
                                  value={e.value}
                                  checked={data.gift.includes(e.value)}
                                  disabled={data.disableGift}
                                ></input>
                              </React.Fragment>
                            )
                        )}
                        <label htmlFor="form__add__noGift">
                          Khng phi l qu tng
                        </label>
                        <input
                          defaultChecked
                          className="gift"
                          id="form__add__noGift"
                          type="checkbox"
                          value={""}
                          onChange={(e) => {
                            if (e.target.checked) {
                              setData({
                                ...data,
                                gift: [],
                                disableGift: e.target.checked,
                              });
                            } else {
                              setData({
                                ...data,
                                disableGift: e.target.checked,
                              });
                            }
                          }}
                        ></input>
                      </li>
                      <li className="form__item form__item__type__colors">
                        <p>Mu sc:</p>
                        {dataInput.map(
                          (e, id) =>
                            e.type === "color" && (
                              <React.Fragment key={id}>
                                <label htmlFor={e.title + id}>{e.title}</label>
                                <input
                                  onChange={(e) => handleEvents.getData(e)}
                                  className="color"
                                  id={e.title + id}
                                  type="checkbox"
                                  value={e.value}
                                  checked={data.color.includes(e.value)}
                                ></input>
                              </React.Fragment>
                            )
                        )}
                      </li>{" "}
                      <li className="form__item ">
                        <input
                          onChange={(e) => handleEvents.getData(e)}
                          value={data.price}
                          type="number"
                          placeholder="Gi (VND)"
                          maxLength="20"
                          min="1000"
                          className="price"
                        />
                      </li>
                      <li className="form__item">
                        <textarea
                          onChange={(e) => handleEvents.getData(e)}
                          value={data.description}
                          type="text"
                          className="description"
                          placeholder="M t chi tit sn phm"
                        />
                      </li>
                    </FormAdd>
                  </Modal>
                ) : (
                  ""
                )}

                {/* =============end add product ================== */}

                {/* =============start update product ================== */}

                {isActiveForm ? (
                  <Modal
                    style={{
                      backgroundImage:
                        "linear-gradient(-20deg, #00cdac 0%, #8ddad5 100%)",
                    }}
                    active={isActiveForm}
                    setActive={setIsActiveForm}
                  >
                    <FormEdit
                      handleClick={notify}
                      title="Cp nht thng tin sn phm"
                      onSubmit={() => updateProducts(data._id)}
                    >
                      {loadingOneProduct ? (
                        <Skeleton height="60vh" width="50vw" />
                      ) : (
                        <>
                          <li className="form__item">
                            <input
                              type="file"
                              className="custom__input__file__form__add"
                              id="custom__input__file__form__add"
                              multiple
                              accept="image/png,image/jpeg"
                              onChange={(e) => handleEvents.changeImg(e)}
                            />
                            <label
                              htmlFor="custom__input__file__form__add"
                              className="label__custom__input__file__form__add"
                            >
                              Thm nh
                            </label>
                          </li>
                          <li className="form__item">
                            <div className="row ">
                              {handleEvents.previewAvatar(avatar)}
                            </div>
                          </li>
                          <li className="form__item mt-4">
                            <input
                              onChange={(e) => handleEvents.getData(e)}
                              value={data.name}
                              type="text"
                              placeholder="Tn sn phm"
                              style={{ width: "100%" }}
                              className="name"
                            />
                          </li>
                          <li className="form__item">
                            <p>Loi sn phm:</p>
                            <select
                              ref={typeRef}
                              onChange={(e) => {
                                handleEvents.changType(e);
                                handleEvents.getData(e);
                              }}
                              value={data.ty}
                              name="edit__type_product"
                              id="type_product"
                              className="type"
                            >
                              <option value="Trang sc">Trang sc</option>
                              <option value="ng h">ng h</option>
                              <option value="Ph kin">Ph kin</option>
                            </select>
                          </li>
                          <li className="form__item form__item__category">
                            {cateJewel === true && (
                              <>
                                <p>Loi trang sc:</p>
                                <select
                                  onChange={(e) => handleEvents.getData(e)}
                                  value={data.category.jewel_type}
                                  className="jewel_type"
                                >
                                  <option value="Nhn">Nhn</option>
                                  <option value="Lc">Lc</option>
                                  <option value="Bng tai">Bng tai</option>
                                  <option value="Vng tay">Vng tay</option>
                                  <option value="Dy c">Dy c</option>
                                </select>
                                <br />
                                <p>Dng trang sc:</p>
                                <select
                                  className="jewel_line"
                                  onChange={(e) => handleEvents.getData(e)}
                                  value={data.category.jewel_line}
                                >
                                  <option value="Kim cng">Kim cng</option>
                                  <option value="Khng nh ">
                                    Khng nh 
                                  </option>
                                  <option value="Ecz-cz">Ecz-cz</option>
                                </select>
                              </>
                            )}
                            {cateWatch === true && (
                              <>
                                <p>Thng hiu:</p>
                                <select
                                  id="brand__watch"
                                  className="brand"
                                  onChange={(e) => handleEvents.getData(e)}
                                  value={data.category.brand}
                                >
                                  <option value="Casio">Casio</option>
                                  <option value="Gucci">Gucci</option>
                                  <option value="Citizen">Citizen</option>
                                </select>
                              </>
                            )}
                            {cateAccessory === true && (
                              <>
                                <p>Loi ph kin:</p>
                                <select
                                  id="type_accessory"
                                  className="accessory_type"
                                  onChange={(e) => handleEvents.getData(e)}
                                  value={data.category.accessory_type}
                                >
                                  {" "}
                                  <option value="Mt knh">Mt knh</option>
                                  <option value="Tht Lng">Tht Lng</option>
                                </select>
                              </>
                            )}
                          </li>
                          <li className="form__item">
                            <p>Cht liu:</p>
                            <select
                              className="material"
                              name="edit__material"
                              id="material"
                              ref={goldRef}
                              onChange={(e) => {
                                handleEvents.getData(e);
                                handleEvents.changMaterial(e);
                              }}
                              value={data.category.material}
                            >
                              <option value="Vng">Vng</option>
                              <option value="Bc">Bc</option>
                              <option value="Hp kim cao cp">
                                Hp kim cao cp
                              </option>
                              <option value="Platinum">Platinum</option>
                            </select>
                            <br />
                            {activeGold === true && (
                              <>
                                <p>Cht liu vng:</p>
                                <select
                                  name="material__gold"
                                  id="material__gold"
                                  className="materialGold"
                                  onChange={(e) => handleEvents.getData(e)}
                                  value={data.category.materialGold}
                                >
                                  <option value="18k">18k</option>
                                  <option value="22k">22k</option>
                                  <option value="24k">24k</option>
                                </select>
                              </>
                            )}
                          </li>
                          <li className="form__item">
                            <p>Gii tnh:</p>
                            <select
                              id="gender"
                              className="gender"
                              onChange={(e) => handleEvents.getData(e)}
                              value={data.gender}
                            >
                              <option value="Nam">Nam</option>
                              <option value="N">N</option>
                              <option value="Unisex">Unisex</option>
                            </select>
                          </li>
                          <li className="form__item">
                            <p>Sn phm couple:</p>
                            <select
                              id="couple"
                              className="isCouple"
                              onChange={(e) => handleEvents.getData(e)}
                              value={data.isCouple}
                            >
                              <option value={true}>C</option>
                              <option value={false}>Khng</option>
                            </select>
                          </li>
                          <li className="form__item form__item__type__gifts">
                            <p>Loi qu tng: </p>

                            {dataInput.map(
                              (e, id) =>
                                e.type === "gift" && (
                                  <React.Fragment key={id}>
                                    <label htmlFor={e.title + id}>
                                      {e.title}
                                    </label>
                                    <input
                                      onChange={(e) => handleEvents.getData(e)}
                                      className="gift"
                                      id={e.title + id}
                                      type="checkbox"
                                      value={e.value}
                                      checked={data.gift.includes(e.value)}
                                      disabled={data.disableGift}
                                    ></input>
                                  </React.Fragment>
                                )
                            )}
                            <label htmlFor="form__add__noGift">
                              Khng phi l qu tng
                            </label>
                            <input
                              defaultChecked
                              className="gift"
                              id="form__add__noGift"
                              type="checkbox"
                              value={""}
                              onChange={(e) => {
                                if (e.target.checked) {
                                  setData({
                                    ...data,
                                    gift: [],
                                    disableGift: e.target.checked,
                                  });
                                } else {
                                  setData({
                                    ...data,
                                    disableGift: e.target.checked,
                                  });
                                }
                              }}
                            ></input>
                          </li>
                          <li className="form__item form__item__type__colors">
                            <p>Mu sc:</p>
                            {dataInput.map(
                              (e, id) =>
                                e.type === "color" && (
                                  <React.Fragment key={id}>
                                    <label htmlFor={e.title + id}>
                                      {e.title}
                                    </label>
                                    <input
                                      onChange={(e) => handleEvents.getData(e)}
                                      className="color"
                                      id={e.title + id}
                                      type="checkbox"
                                      value={e.value}
                                      checked={data.color.includes(e.value)}
                                    ></input>
                                  </React.Fragment>
                                )
                            )}
                          </li>{" "}
                          <li className="form__item ">
                            <input
                              onChange={(e) => handleEvents.getData(e)}
                              value={data.price}
                              type="number"
                              placeholder="Gi (VND)"
                              maxLength="20"
                              min="1000"
                              className="price"
                            />
                          </li>
                          <li className="form__item">
                            <textarea
                              onChange={(e) => handleEvents.getData(e)}
                              value={data.description}
                              type="text"
                              className="description"
                              placeholder="M t chi tit sn phm"
                            />
                          </li>
                          <li className="form__item mt-4">
                            <Button
                              onClick={() => {
                                updateProduct();
                                setTimeout(() => {
                                  setIsActiveForm(false);
                                }, 1000);
                              }}
                              content="Cp nht sn phm"
                            />
                          </li>
                        </>
                      )}
                    </FormEdit>
                  </Modal>
                ) : (
                  ""
                )}

                {/* =============end update product ================== */}

                <table className="p-4">
                  <thead>
                    <tr>
                      <td>STT</td>
                      <td>Hnh nh</td>
                      <td>Tn sn phm</td>
                      <td>Gi</td>
                      <td>Thi gian cp nht</td>
                      <td>Chnh sa</td>
                      <td>Xa</td>
                    </tr>
                  </thead>
                  <tbody>
                    {products
                      .filter((val) => {
                        if (searchItem === "") {
                          return val;
                        } else if (
                          val.name
                            .toLowerCase()
                            .includes(searchItem.toLowerCase())
                        ) {
                          return val;
                        }
                      })
                      .map((e, id) => (
                        <tr key={id}>
                          <td>{id + 1}</td>
                          <td>
                            <img src={`../../images/${e.imgsUrl[0]}`} alt="" />
                          </td>
                          <td>{e.name}</td>
                          <td>{numberWithCommas(e.price)}</td>
                          <td>{moment(e.updatedAt).format("LLL")}</td>
                          <td>
                            <i
                              className="bx bx-edit-alt"
                              onClick={() => {
                                setIsActiveForm(true);
                                setCurrentId(e._id);
                              }}
                            ></i>
                          </td>
                          <td>
                            <i
                              className="bx bx-trash"
                              onClick={() => {
                                deleteProducts(e._id);
                                notifyDel();
                              }}
                            ></i>
                          </td>
                        </tr>
                      ))}
                  </tbody>
                </table>
              </Table>
            </div>
          </div>
        </div>
      </div>
    </Helmet>
  );
};

export default ProductAdmin;
